import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Rating } from "primereact/rating";
import img1 from "../imgs/img_granola.jpg";
import img2 from "../imgs/img_granolanguyenban.jpg";
import img3 from "../imgs/img_granolasocola.jpg";
import img4 from "../imgs/img_mixnhiu.jpg";
import { Button } from "primereact/button";
import { useState, useEffect, useRef } from "react";
import { Dialog } from "primereact/dialog";
import Carts from "../Carts";
import { Toast } from "primereact/toast";
function Products({
  giatri,
  value,
  show,
  show2,
  setSoluong,
  showDialog,
  setShowDialog,
}) {
  const toast = useRef(null);
  const show1 = () => {
    toast.current.show({
      severity: "success",
      summary: "Thng bo!",
      detail: "Thanh ton thnh cng",
    });
  };
  const show5 = () => {
    toast.current.show({
      severity: "success",
      summary: "Announcement!",
      detail: "Successful payment",
    });
  };
  const [clicks, setClick] = useState([]);
  const [count, setCount] = useState(0);
  const data = [
    {
      id: 1,
      name: "GRANOLA MATCHA",
      price: 140,
      rating: 3,
      img: img1,
      quantity: 1,
      inventoryStatus: "Cn hng",
      category: "Cn hng",
    },
    {
      id: 2,
      name: "GRANOLA SOCOLA",
      price: 150,
      rating: 4,
      img: img3,
      quantity: 1,
      inventoryStatus: "Cn hng",
      category: "Cn hng",
    },
    {
      id: 3,
      name: "GRANOLA NGUYN BN",
      price: 120,
      rating: 5,
      img: img2,
      quantity: 1,
      inventoryStatus: "Cn hng",
      category: "Cn hng",
    },
    {
      id: 4,
      name: "OPTIONAL NUTS",
      price: 160,
      rating: 3,
      img: img4,
      quantity: 1,
      inventoryStatus: "Mix theo yu cu",
      category: "Cn hng",
    },
  ];
  const imageBodyTemplate = (product) => {
    return (
      <img src={product.img} alt="" className="w-6rem shadow-2 border-round" />
    );
  };
  const priceBodyTemplate = (product) => {
    return <h3>{product.price}.000 VND</h3>;
  };
  const ratingBodyTemplate = (product) => {
    return <Rating value={product.rating} readOnly cancel={false} />;
  };
  const buttonBodyTemplate = (data) => {
    return (
      <Button
        onClick={() => {
          setCount(count + 1);
          if (clicks.length === 0) {
            setClick([...clicks, data]);
          } else {
            let check = clicks.find((item) => item.id === data.id);
            if (!check) {
              setClick([...clicks, data]);
            } else {
              let index = clicks.findIndex((item) => item.id === data.id);
              clicks[index].quantity += 1;
            }
          }
          value === "VNM" ? show() : show2();
        }}
        label={value === "VNM" ? "Mua" : "Buy"}
      />
    );
  };
  useEffect(() => {
    let res = clicks.reduce((total, currentValue) => {
      return total + currentValue.quantity;
    }, 0);
    setSoluong(res);
  }, [count]);
  console.log(clicks);
  return (
    <div>
      <Toast ref={toast} position="top-left" />
      <DataTable
        value={data}
        filters={giatri}
        tableStyle={{ minWidth: "60rem" }}
        paginator
        currentPageReportTemplate="Hin th {first} n {last} trong s {totalRecords} sn phm"
        rows={10}
        rowsPerPageOptions={[2, 3, 4]}
        className="datatable-responsive"
        paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
      >
        <Column field="id" header="STT"></Column>
        <Column
          field="name"
          header={value === "VNM" ? "Tn sn phm" : "Name"}
        ></Column>
        <Column
          header={value === "VNM" ? "nh sn phm" : "Image"}
          body={imageBodyTemplate}
        ></Column>
        <Column
          field="price"
          body={priceBodyTemplate}
          header={value === "VNM" ? "Gi sn phm" : "Price"}
        ></Column>
        <Column
          header={value === "VNM" ? "Mua ngay" : "Buy"}
          body={buttonBodyTemplate}
        ></Column>
        <Column
          field="rating"
          header={value === "VNM" ? "nh gi" : "Rating"}
          body={ratingBodyTemplate}
        ></Column>
      </DataTable>
      <div>
        <Dialog visible={showDialog} onHide={() => setShowDialog(false)}>
          <Carts
            clicks={clicks}
            setCount={setCount}
            setShowDialog={setShowDialog}
            count={count}
            show1={show1}
            value={value}
            show5={show5}
            setSoluong={setSoluong}
          />
        </Dialog>
      </div>
    </div>
  );
}
export default Products;
